import React, { Component } from 'react';
import './App.css';

import { Header, Section, Button, Rectangle, Footer, Banner } from './components';
import StyledMain from './App.style';

/* eslint-disable import/no-webpack-loader-syntax */
import docSVG from '-!svg-react-loader!./assets/doc.svg';
import interviewSVG from '-!svg-react-loader!./assets/interview.svg';
import manDancingSVG from '-!svg-react-loader!./assets/man-dancing.svg';
import publicSpeakerSVG from '-!svg-react-loader!./assets/public-speaker.svg';
import magSVG from '-!svg-react-loader!./assets/magnifier.svg';
import teleSVG from '-!svg-react-loader!./assets/telephone.svg';
/* eslint-enable import/no-webpack-loader-syntax */
import pioneerLaborLogo from './assets/pioneer-labor-logo.jpg';

class App extends Component {
  render() {
    return (
      <StyledMain>
        <Header />
        <Banner className="index-banner"/>
        <div className="container">
          <Section className="section1">
            <div className="left">
              <h2 className="title"></h2>
              <p>2017  11  OO  11 </p>
              <p></p>
            </div>
            <div className="right">
            </div>
          </Section>
          <Section className="section2">
            <div className="right">
              <h2 className="title"></h2>
              <p></p>
              <p> 7%</p>
              <p></p>
            </div>
            <div className="left">
            </div>
          </Section>
          <Section className="tight-section">
            <h2 className="align-center"></h2>
            <p><span className="red-text"></span></p>
            <p></p>
            <p>
              <ul>
                <li> <span className="yellow-text"></span></li>
                <li> <span className="yellow-text"></span></li>
                <li> <span className="yellow-text"></span></li>
              </ul>
            </p>
            <p><br /> 30 </p>
            <div className="align-center">
              <Button></Button>
            </div>
          </Section>
          <Section className="section3">
            <h2 className="align-center"></h2>
            <div className="align-center flex flow-container">
              <Rectangle
                SVGIcon={docSVG}
                title='1. '
                content=''
              />
              <Rectangle
                SVGIcon={manDancingSVG}
                title='2. '
                content=''
              />
              <Rectangle
                SVGIcon={interviewSVG}
                title='3. '
                content=''
              />
              <Rectangle
                SVGIcon={publicSpeakerSVG}
                title='4. '
                content=''
              />
            </div>
            <div className="tight-section">
              <p></p>
            </div>
          </Section>
          <Section className="align-center">
            <h2></h2>
            <div>
              <img src={pioneerLaborLogo} className="org-logo" alt=""/>
              <div></div>
            </div>
          </Section>
          <Section>
            <h2 className="align-center">...</h2>
            <div className="align-center flex">
              <Rectangle
                SVGIcon={docSVG}
                title=''
                content=''
              />
              <Rectangle
                SVGIcon={magSVG}
                title=''
              />
              <Rectangle
                SVGIcon={teleSVG}
                title=''
                content=''
              />
            </div>
          </Section>
        </div>
        <Footer />
      </StyledMain>
    );
  }
}

export default App;



